@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{padding:50px 0;font-family:"Open Sans", sans-serif;width:100%;height:100%;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:#f4f4f6}

.main{max-width:1300px;border-radius:8px;box-shadow:0 4px 26px 0 #e1e1e1}.main-block{padding:30px 45px;text-align:left}.main__header{font-size:14px;color:#6d7278;display:flex;margin-bottom:15px}.main__header-item{margin-right:15px}.main__header-item_access{color:#8a7c5d;display:flex}.main__header-item_access img{margin-right:8px}.main__info-title{margin-bottom:8px;font-size:30px}.main__info-description{font-size:16px;line-height:1.5}.main__banner{width:100%}.main__banner img{width:100%;display:block}.main__accordion{border-bottom-left-radius:8px;border-bottom-right-radius:8px;overflow:hidden}.main__accordion-item{padding:30px 45px;background-color:#142536;color:white;font-size:22px;display:flex;align-items:center;justify-content:left;cursor:pointer;border:1px solid #405265}.main__accordion-item:hover{background-color:#22374b}.main__accordion-arrow{background-image:url(/static/media/arrow.849ad399.svg);width:16px;height:9px;margin-left:auto}.main__accordion-arrow_active{transform:rotate(180deg)}.main__accordion-open{padding:30px 45px 45px;background-color:#142536;position:relative}.main__accordion-description{width:60%;font-size:16px;color:white;text-align:left}.main__accordion-pic{position:absolute;right:15%;top:30px}.main__accordion-input{margin-top:30px;width:280px;position:relative}.main__accordion-input input{height:32px;width:100%;border:none;outline:none;border-bottom:solid 1px #5c738b;background-color:#142536;font-size:16px;color:white}.main__accordion-input input:focus{border-bottom-color:white}.main__accordion-button{background-color:#142536;width:214px;height:50px;border-radius:29.5px;border:solid 1px white;color:white;font-size:16px;text-align:center;margin-top:45px;cursor:pointer;outline:none}.main__accordion-button:hover{background-color:white;border-color:#142536;color:#142536}.clear{background-image:url(/static/media/close.590f1bc0.svg);width:14px;height:14px;border:none;outline:none;background-color:#142536;cursor:pointer;position:absolute;right:0;top:8px}.link{color:#5a88c1;border-bottom:1px dashed #5a88c1;text-decoration:none}.link:hover{opacity:0.8}@media (min-width: 768px) and (max-width: 1024px){.main__accordion-pic{right:5%;top:calc(50% - 125px)}}@media (min-width: 320px) and (max-width: 767px){.main{width:100%}.main-block{padding:30px 15px 15px 15px}.main__accordion-block{width:100%}.main__accordion-item{padding:15px}.main__accordion-arrow{margin-left:30px}.main__accordion-text{width:calc(100% - 45px)}.main__accordion-description{width:100%}.main__accordion-open{display:block;padding:15px}.main__accordion-pic{display:flex;position:relative;justify-content:center;margin:30px 0 15px 0;right:auto;top:auto}.main__accordion-input{width:100%}.main__accordion-button{width:100%;margin-bottom:30px}.main .mobile{display:none}}

